@font-face {
	font-family: FiraGO-Regular;
	src: url("../../assets/fonts/FiraGO-Regular.woff2")
}

@font-face {
    font-family: 'GLKirovi-Bold';
    src: url('../../assets/fonts/gl-kirovi-bold.ttf') format('truetype');
    /* Add more src declarations if you have different font file formats */
    /* Specify other properties like font-weight and font-style if needed */
  }


.wm-about-us{
    background-image: url("../../assets/about-bg.png");
    background-size: cover;
    width: 100%;
    height: 100vh;
    background-attachment: fixed;
    -moz-background-size: cover;
    -o-background-size: cover;
    position: absolute;
}

.wm-alazani-text {
    width: 96%;
    background-image: url("../../assets/about-bg-writing.png");
    background-repeat: no-repeat;
    background-position: left;
    height: 100%;
    background-size: contain;
    position: absolute;
    margin-left: 4%;
    top: -46%;
    z-index: 1;
}
.wm-empty{
    height: 45%;
}
.wm-alazani-wrapper{
    display: flex;
    font-size: 30px;
    position: relative;
    background-image: url("../../assets/about-bg.png");
    background-size: cover;
    width: 100%;
    
    background-attachment: fixed;
}

.wm-about-picture {
    width: 100%;
    object-fit: cover;
}
.wm-picture {
    margin-top: 34px;
    width: 45%;
    margin-left: -5%;
    position: absolute;
    z-index: 2; /* Increase z-index value */
  }
  
  .wm-description {
    
    overflow: hidden;
    left: 0%;
    min-height: 400px;
    height: auto;
    margin-top: 0;
    width: calc(100% - 25%);
    background-color: white;
    position: relative; /* Change to relative */
    z-index: 1; /* Decrease z-index value */
  }

.wm-main-text{
    position: relative;
    z-index: 0;

    margin-top: 0px;

    position: relative;
    padding: 40px 20px 40px 60px;
    font-family: FiraGO-Regular;
    line-height: 1.5em;
    font-size: 20px;
    width: 65%;
}

.wm-description-paragraph{
    white-space: pre-line;
}

#wm-component-name{
    color: #fff;
    text-decoration: none;
    font-size: 25px;
    font-family: GLKirovi-Bold;
    font-weight: 700;
    
}
#wm-component-name:visited{
    text-decoration: none;

}

.wm-history{
    position: absolute;
    z-index: 12;
    top: 18%;
    left: 3%;
    text-decoration: none;
    word-spacing: 2px;
    display: flex;
    
}


#wm-angleLeft{
    color: #fff;
    text-decoration: none;
    font-size: 25px;
    font-family: GLKirovi-Bold;
    font-weight: 700;
    margin: auto;
    margin-right: 30px;
}

.wm-img{
    position: absolute;
    top: -30%;
    right: 10%;
    width: 30%;
    z-index: 5;
}
.wm-img>img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 768px) {
    .wm-img{
        position:  static !important;
        width: 100%;
    }
    .wm-history{
        top: 10%;
    }
    .wm-empty{
        height: 280px;
    }
    .wm-alazani-text{
        display: none;
        height: auto;
    }
    .wm-alazani-wrapper{
        display: block;
        position: relative;
        font-size: 0;
        height: auto;
    }
    .wm-picture{
        width: 98%;
        margin-left: 1%;
        position: relative;
    }
    .wm-description{
        margin-top: -10px;
        position: static;
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .wm-main-text{
        position: static;
        

    }


}
